import { Component, Inject, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { BaseDialog } from 'src/app/class/base-dialog';
import { StateService } from 'src/app/service/state.service';
import { SwalService } from 'src/app/service/swal.service';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './deposit-detail-dialog.component.html',
  styleUrls: ['./deposit-detail-dialog.component.css']
})
export class DepositDetailDialogComponent extends BaseDialog{
  _dataSource = new MatTableDataSource([]);
  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<DepositDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {source}) {
      super(injector)
      this._dataSource.data = data.source
    }
    // _columns = ['S No', 'Receipt No','Receipt Date', 'Donation Subtype' ,'Purpose','Amount','Status'];
    _columns = ['S No','Receipt Date','Amount','Status'];
  // onNoClick(): void {
  //   this.dialogRef.close();
  // }
}
