import { Component, EventEmitter, Injector, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { BaseForm } from 'src/app/class/base.form';
import { FormHelperService } from 'src/app/service/form-helper.service';

@Component({
  selector: 'knock-off-filter',
  templateUrl: './knock-off-filter.component.html',
  styleUrls: ['./knock-off-filter.component.css']
})
export class KnockOffFilterComponent extends BaseForm implements OnInit {

  @Output() filterData = new EventEmitter();
  public form : FormGroup;

  constructor(
    public injector: Injector
  ) {
    super(injector)
  }

  ngOnInit(): void {
    this.initForm();
  }


  initForm() {
    this.form = this._fb.group({
      ou:['',this._vs._vals('Operating Unit')],
      dco_receipt : ['', this._vs._val('DCO Receipt', {alphaNum: 1,specialChar:1})],
      person_id : ['', this._vs._val('User ID', {alphaNum: 1})]
    });
  }


  onSubmit(param = '') {
    this.form.markAllAsTouched();
    this._vs._submitted = true;
    let data = this.form.value;
    if (this.form.valid) {
      this.filterData.emit(data);
      this._vs._submitted = false;
    } else {
      return this.form.valid;
    }
  }

}
