import { Component, Inject, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { URLz } from 'src/app/enums/url.enum';
import { BaseDialog } from 'src/app/class/base-dialog';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent extends BaseDialog {
  public response;
  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<UserProfileComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
  ) {
    super(injector)
    dialogRef.disableClose = true;
    this.getData(data);
  }

  _close(isConfirmed = false): void {
    this.dialogRef.close({isConfirmed});
  }

  getData(dt){
    this._http.get({
      endpoint: URLz.USER_PERMISSION_DETAIL,
      query: {
        person_id:dt.id,
        organisation_id:dt.organisation_id,
        system_id:dt.system_id
      }
    }).subscribe((res:any)=>{
      this.response = res?.data?.row;
    })
  }

}
