import { Component, OnInit, Injector, Output, EventEmitter} from '@angular/core';
import { FormHelperService } from '../../../../service/form-helper.service';
import { BaseForm } from 'src/app/class/base.form';
import { FormGroup } from '@angular/forms';


@Component({
  selector: 'e-raseed-filter',
  templateUrl: './e-raseed-collection-filter.component.html',
  styleUrls: ['./e-raseed-collection-filter.component.css']
})
export class ERaseedCollectionFilterComponent extends BaseForm  implements OnInit {

  @Output() filterData = new EventEmitter();
  public form : FormGroup;

  public _data_permission: object = {};
  public _pathpermission: string;
  _fhs: FormHelperService;


  constructor(injector: Injector) {
    super(injector);
    this._fhs = injector.get(FormHelperService);
   }

  ngOnInit(): void {
    this.initForm();
  }

  initForm() {
    this.form = this._fb.group({
      bg: ['', this._vs._vals('Business Group')],
      le: ['', this._vs._vals('Legal Entity')],
      ou: ['', this._vs._vals('Operating Unit')],
      receipt_type: ['', this._vs._vals('Receipt')],
      userId: ['', this._vs._val('User ID', {minChar: 3,maxChar:100})],
    });
  }

  onSubmit() {
    this.form.markAllAsTouched();
    this._vs._submitted = true;
    let data = this.form.value;
    if (this.form.valid) {
      this.filterData.emit(data);
      this._vs._submitted = false;
    } else {
      return this.form.valid;
    }
  }

}
