import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DisregardGuard } from './guard/disregard.guard';
import { MonthlyTargetGuard } from './guard/monthly-target.guard';
import { PermissionGuard } from './guard/permission.guard';
import { SideBarMenusGuard } from './guard/side-bar-menus.guard';
import { StatesGuard } from './guard/states.guard';
import { FullComponent } from './layouts/full/full.component';

const routes: Routes = [
  { path: '', redirectTo: 'dm_dashboard', pathMatch: 'full' },
  {
    path: 'e_raseed_dashboard',
    loadChildren: () =>
      import('./e-raseed-dashboard/e-raseed-dashboard.module')
        .then((m) => m.ERaseedDashboardModule),
  },
  {
    path: 'blank',
    loadChildren: () =>
      import('./blank/blank.module')
        .then((m) => m.BlankModule),
  },
  {
    path: '',
    component: FullComponent,
    canActivateChild: [
      // MonthlyTargetGuard,
      // StatesGuard,
      SideBarMenusGuard,
      DisregardGuard, // USE UNTIL ANGULAR DOESN'T PROVIDE CanDeActivateChild GUARD
      PermissionGuard,
    ],
    children: [
      {
        path: 'dm_dashboard',
        loadChildren: () =>
          import('./dm-dashboard/dm-dashboard.module')
            .then((m) => m.DmDashboardModule),
      },
      {
        path: '',
        loadChildren: () =>
          import('./feature/feature.module')
            .then((m) => m.FeatureModule),
      },

    ],
  },
  {
    path: '**',
    redirectTo: '/error/404',
    pathMatch: 'full'
  },
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {

}
