import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BaseServiceInjector } from 'src/app/class/base-service-injector';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './verification-confirmation.component.html',
  styleUrls: ['./verification-confirmation.component.css']
})
export class VerificationConfirmationComponent extends BaseServiceInjector  implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<VerificationConfirmationComponent>,
    public _router: Router,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public injector: Injector
  ) {
      super(injector);
   }

  ngOnInit(): void {
  }

  _close(): void {
    this.dialogRef.close();
  }
  Switch(ver_no,receipt_type){
    let url;
    let path = '/blank/acknowledgement_color';
    url = this._router.serializeUrl(
      // this._router.createUrlTree([path, { slipId: ack_no , rec_t:receipt_type}])
      this._router.createUrlTree([path, {rec_t:receipt_type,ver_no:ver_no, type:'ver'}])
    );
    window.open(url, '_blank');
  }
  applyClass = { 'col-lg-3': false, 'col-md-4': false, 'col-md-12': true };

}
