import { Component, Inject, Injector, OnInit ,  OnDestroy } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { BaseForm } from 'src/app/class/base.form';
import { FormHelperService } from 'src/app/service/form-helper.service';
import { RegExps } from 'src/app/static/regex';
@Component({
  selector: 'app-cash-deposit-filter',
  templateUrl: './cash-deposit-filter.component.html',
  styleUrls: ['./cash-deposit-filter.component.css']
})
export class CashDepositFilterComponent extends BaseForm implements OnDestroy, OnInit {

  selected: any;
  form : FormGroup;
  constructor(
    private helper : FormHelperService,
    public dialogRef: MatDialogRef<CashDepositFilterComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public injector: Injector
    ) {
      super(injector)
      dialogRef.disableClose = true;
    }

  ngOnInit(): void {
    this.initForm();
  }
  date :FormControl

  initForm() {
    this.form = this._fb.group({
      selected: [],
      cashAcc: [''],
      purpose:[''],
      branchNo:[''],
      orgId:['',this._vs._val('Donation Org')],
      systemId:['',this._vs._val('Organization System')],
    });

   this.date =  this.form.get('selected') as FormControl;
   this.date.addValidators([this._vs._vals('Date'),this._vs._val_Date({})]);
   this.date.updateValueAndValidity();
  }
  onNoClick(): void {
    this.dialogRef.close(this.data);
  }
  onSubmit() {
    this.form.markAllAsTouched();
    this._vs._submitted = true;

    if(this.form.valid){
      if(this.form.get('selected').value.startDate !== '' && this.form.get('selected').value.endDate !== ''){
        if(!RegExps.DATE_RANGE.test(this.form.get('selected').value.startDate) && !RegExps.DATE_RANGE.test(this.form.get('selected').value.endDate)){
          this.data.startDate = this.form.get('selected').value.startDate.format("YYYY-MM-DD");
          this.data.endDate = this.form.get('selected').value.endDate.format("YYYY-MM-DD");
        }
      }

      this.data.cashAcc = this.form.get('cashAcc').value != ''  ? this.form.get('cashAcc').value : '';
      this.data.branchNo = this.form.get('branchNo').value != '' ? this.form.get('branchNo').value : '';
      this.data.orgId = this.form.get('orgId').value != '' ? this.form.get('orgId').value : '';
      this.data.purpose = this.form.get('purpose').value != '' ?  this.form.get('purpose').value : '';
      this.data.systemId = this.form.get('systemId').value != '' ? this.form.get('systemId').value : '';
      this.dialogRef.close(this.data);
      this._vs._submitted = false;
    }
    else{
      return this.form.valid;
    }
  }



}
