import { Component, Inject, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { Constant } from 'src/app/constant/constant';
import { URLz } from 'src/app/enums/url.enum';
import { Custom } from 'src/app/static/custom';
import { environment } from 'src/environments/environment';
import { HTTPService } from '../../../../service/http.service';
import { SwalService } from 'src/app/service/swal.service';
import { BaseDialog } from 'src/app/class/base-dialog';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './batch-detail-dialog.component.html',
  styleUrls: ['./batch-detail-dialog.component.css']
})
export class BatchDetailDialogComponent extends BaseDialog {
  _dataSource = new MatTableDataSource([]);
  public _batch_id : unknown;
  public queryquery ?: any;

  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<BatchDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {source, params},

    ) {
      super(injector)
      this._batch_id = data.source.id;
      this.getData(data.source);
    }
    _columns = ['Donation_type','Amount','Action'];
    getData(dt){
      this._http.get({
        endpoint: URLz.BATCH_REPORTS_DETAIL,
        query: {
          person_id:dt.user_id,
          batch_id:dt.id,
        }
      }).subscribe((res:any)=>{
          if(res.data.records.length > 0){
            this._dataSource.data = res?.data?.records;
          }
      })
    }

    Switch(item){
        this.queryquery = {
          ...Constant.defaultJasperParams,
          reportUnit: environment.J_DONATION_SERVER+'/'+environment.J_REPORT+'/UserBatchReportNew',
          batch_id: this._batch_id,
        }
        this.ReceiptType(item);
        window.open(environment.JASPER_URL + URLz.REPORT_URL+ '?' + Custom.objToURLQuery(this.queryquery), '_blank');
        // window.location.href = environment.JASPER_URL + URLz.REPORT_URL+ '?' + Custom.objToURLQuery(this.queryquery);
    }

    ReceiptType(item){
      if(item.title == 'Cash & Cheque'){
        return  this.queryquery.receipt_type = '(' + '1,2' + ')';
      }
      else if(item.title == 'Direct Deposit'){
        return  this.queryquery.receipt_type = '(' + '3' + ')';
      }
      else if(item.title == 'Material'){
        return this.queryquery.receipt_type = '(' + '4' + ')';
      }
      else if(item.title == 'All'){
        return this.queryquery.receipt_type = '(' + '1,2,3,4' + ')';
      }
    }
}

