import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './box-deposit-dialog.component.html',
  styleUrls: ['./box-deposit-dialog.component.css']
})
export class BoxDepositDialogComponent  extends BaseListClass implements OnInit{

  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<BoxDepositDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {source}
  ) {
    super(injector);

  }

  ngOnInit() {
    this._component = 'Sell Deposit';
    this.initTables();
  }

  initTables(){
    this._reset(URLz.BOX_SELL)
    this._tbls[URLz.BOX_SELL].columns = [
      'id',
      'actions',
      'date',
      'bg',
      'le',
      'ou',
      'su',
      'department_id',
      'name',
      'mobile',
      'followup',
      'address',
      'near_by',
      'reference_no',
      'box_qty',
      'price_per_box',
      'collector_id',
    ];
    this._tbls[URLz.BOX_SELL].endpoint = URLz.BOX_SELL
    this._formCreator(this._tbls[URLz.BOX_SELL])
    this._tbls[URLz.BOX_SELL].formFilter = this._fb.group({
      deposit_id:[this.data.source],

    })
    this._refresh(URLz.BOX_SELL)
  }


}
