import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BaseForm } from 'src/app/class/base.form';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './transaction-confirmation.component.html',
  styleUrls: ['./transaction-confirmation.component.scss'],
  host: {class: 'col-lg-6 col-sm-12 p-0'}
})
export class TransactionConfirmationComponent extends BaseForm implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<TransactionConfirmationComponent>,
    public _router: Router,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public injector: Injector
  ) {
    super(injector);
      }
  ngOnInit(): void {}
  _close(): void {
    this.dialogRef.close();
  }
  Switch(id,slip){
    let url;
    if(id){
      if(window.location.href.indexOf('material') != -1) url = 'material'
      else url = 'cash_cheque'
      url = this._router.createUrlTree(['/blank/' + url + slip, { slipId: id }])
      window.open(url, '_blank');
      // window.open('http://localhost:4200/blank/cash_cheque_color;slipId=562', '_blank');
    }
  }
  applyClass = { 'col-lg-3': false, 'col-md-4': false, 'col-md-12': true };
}
