import { Component, Inject, Injector} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { BaseDialog } from 'src/app/class/base-dialog';
import { StateService } from 'src/app/service/state.service';
import { SwalService } from 'src/app/service/swal.service';

@Component({
  selector: 'user-donation-dialog',
  templateUrl: './user-donation-dialog.component.html',
  styleUrls: ['./user-donation-dialog.component.css']
})
export class UserDonationDialogComponent extends BaseDialog {
  _dataSource = new MatTableDataSource([]);
  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<UserDonationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {source, title}) {
    super(injector)
      this._dataSource.data = data.source
    }

  _columns = [
    'S No',
    'Receipt No',
    'Amount',
  ];
}
