import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BaseDialog } from 'src/app/class/base-dialog';
import { SwalService } from 'src/app/service/swal.service';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './collection-confirmation.component.html',
  styleUrls: ['./collection-confirmation.component.css'],
  host: {class: 'col-lg-6 col-sm-12 p-0'}
})
export class CollectionConfirmationComponent extends BaseDialog implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<CollectionConfirmationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public injector: Injector
  ) {
    super(injector)
  }
  ngOnInit(): void {

  }
  _close(): void {
    this.dialogRef.close();
  }
  Switch(ack_no,receipt_type){
    let url;
    let path ;
    receipt_type == 1 ? path = '/blank/acknowledgement_color' : path = '/blank/ack_material_color';
    url = this._router.serializeUrl(
      this._router.createUrlTree([path, { slipId: ack_no , rec_t:receipt_type}])
    );
    window.open(url, '_blank');
  }
  applyClass = { 'col-lg-3': false, 'col-md-4': false, 'col-md-12': true };
}
