import { HttpErrorResponse } from '@angular/common/http';
import { Component, Inject, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './box-mgmt-stock.component.html',
  styleUrls: ['./box-mgmt-stock.component.css'],
})
export class BoxMgmtStockComponent extends BaseForm {
  systemSubscription: any;
  constructor(
    public dialogRef: MatDialogRef<BoxMgmtStockComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public injector: Injector
  ) {
    super(injector);
    dialogRef.disableClose = true;
    this.initForm();
  }
  _close(action: 'success' | 'close'): void {
    this.dialogRef.close({ data: this.data, action});
  }
  _process() {
    const data = this._fs._form.value
    this._http.create({
        body: data,
        query: {parent_id: this?.data?.data?.id},
        endpoint: this.data.popupType == 1 ? URLz.BOX_OU_ISSUE : URLz.BOX_COL_ISSUE
    }).subscribe({
      next: () => {
        this._close(('success'))
      },
      error: (httpErrorResponse: HttpErrorResponse) => {
        this._vs._error_server(httpErrorResponse.error);
      }
    });
  }
  initForm() {
    const currentDate = this._fhs._dateOnly()
    const max = this?.data?.data?.remaining
    if (this.data.popupType == 1) {
      this._fs._form = this._fb.group({
        date: [currentDate, this._vs._vals('Date')],
        box_qty: ['', this._vs._val('Box Quantity', { max, min: 1})],
        bg: ['', this._vs._vals('Business Group')],
        le: ['', this._vs._vals('Legal Entity')],
        ou: ['', this._vs._vals('Operating Unit')]
      });
    } else {
      this._fs._form = this._fb.group({
        date: [currentDate, this._vs._vals('Date')],
        box_qty: ['', this._vs._val('Box Quantity', { max, min: 1})],
        su: ['', this._vs._vals('Sub Unit')],
        collector_id: ['',this._vs._vals('Collector')],
      });
    }
  }
  applyClass = { 'col-lg-3': false, 'col-md-4': false, 'col-md-12': true };
}
