import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BaseDialog } from 'src/app/class/base-dialog';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './monthly-target.component.html',
  styleUrls: ['./monthly-target.component.css'],
})
export class MonthlyTargetComponent extends BaseDialog implements OnInit {
  public month;
  constructor(
    injector: Injector,
    public dialogRef: MatDialogRef<MonthlyTargetComponent>,
    @Inject(MAT_DIALOG_DATA) public data : {source}) {

      super(injector)
    }
  ngOnInit(): void {
    const date = new Date();
    this.month = date.toLocaleString('default', { month: 'long' });
  }
  _close(): void {
    this.dialogRef.close();
  }
}

