import { Component, Inject, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { BaseDialog } from 'src/app/class/base-dialog';
@Component({
  selector: 'app-mat-full-dialog',
  templateUrl: './mat-full-dialog.component.html',
  styleUrls: ['./mat-full-dialog.component.css']
})
export class MatFullDialogComponent extends BaseDialog{
  _dataSource = new MatTableDataSource([]);
  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<MatFullDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {source, title}
    ) {
      super(injector)
      this._dataSource.data = data.source;
    }


  // onNoClick(): void {
  //   this.dialogRef.close();
  // }
  _columns = [
    'id',
    'organisation',
    'system'
  ];
}
