import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SwalService } from 'src/app/service/swal.service';
import { IMG_URL } from 'src/app/constant/image';
import { BaseDialog } from 'src/app/class/base-dialog';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './box-mgmt-sell.component.html',
  styleUrls: ['./box-mgmt-sell.component.css']
})
export class BoxMgmtSellComponent extends BaseDialog {
  IMG_URL = IMG_URL;
  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<BoxMgmtSellComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    super(injector)
    dialogRef.disableClose = true;
  }
  _close(): void {
    this.dialogRef.close();
  }
  _selected(item): void {
    this.dialogRef.close(item);
  }
}
