import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { BaseDialog } from 'src/app/class/base-dialog';
import { Transaction } from 'src/app/model/transaction/full.form';
import { StateService } from 'src/app/service/state.service';
import { SwalService } from 'src/app/service/swal.service';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './transaction-confirm.component.html',
  styleUrls: ['./transaction-confirm.component.css'],
})
export class TransactionConfirmComponent extends BaseDialog implements OnInit{
  _dataSource = new MatTableDataSource([]);
  constructor(
    public injector: Injector,
    public dialogRef: MatDialogRef<TransactionConfirmComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Transaction
  ) {
    super(injector)
    this._dataSource.data = data.receipt_details.filter(x => !x.row_id);
  }
  _columns = []
  ngOnInit(): void {
      this._columns = [
        'id',
        'amount',
        'donation_type',
        'donation_sub_type',
        'purpose',
        'majlis',
        //'donationMode',
        'location',
        'additionalInfo',
      ];
      // Material Case
      if(this.data.receipt_type == 4){
        this._columns.splice(4, 0, 'material_item', 'weight')
      }else if(this.data.receipt_type == 1) {
        this._columns.splice(7, 0, 'account', 'bank', 'branchCode')
      }else if(this.data.receipt_type == 3) {
        this._columns.splice(7, 0, 'account', 'branchCode')
      }
  }
  _close(action: boolean): void {
    this.dialogRef.close(action);
  }

}
